<template>
  <ul class="posts-list">
    <li v-for="item in topics">
      <router-link to="/topic/5683520e59ec59521f2f1731" :title="item.title" :class="setListClass(item.cover_url.length, item.istop)">
        <h1 v-text="item.title" class="title">
            </h1>
        <div class="pics">
          <div class="pic" v-if="!item.istop && item.cover_url.length >= 3" v-for="pic in item.cover_url.slice(0, 3)">
            <img class="banner" :src="pic">
          </div>

          <div class="pic" v-if="item.istop || !item.istop && item.cover_url.length < 3" v-for="pic in item.cover_url.slice(0, 1)">
            <img class="banner" :src="pic">
          </div>
        </div>
        <div class="footer">
          <div class="info">
            <div class="tags" v-for="tag in item.tags.slice(0, 2)">{{tag}}</div>
            <div class="comment">评论({{item.comments}})</div>
            <div class="goods">赞({{item.praises}})</div>
          </div>
          <div class="range">{{item.distance}}km</div>
        </div>
      </router-link>
    </li>
  </ul>
</template>
<style lang="scss">
@import '~style/base/_mixin';

.posts-list {
  li {
    padding: 10px 0;
    margin: 0 10px;
    border-bottom: 1px solid #d5dbdb;
    &:last-child {
      border-bottom: none;
    }
    a {
      display: block;
      overflow: hidden;
      position: relative;
    }
  }

  h1 {
    font-size: 18px;
    line-height: 1.3;
    color: #000;
    position: relative;
    @include title;
  }
  // 一张大图
  .top {
    .pics {
      margin-top: 5px;
    }
    .pic {
      height: 175px;
    }
  }

  // 显示三张图片
  .medium {
    .pics {
      margin-top: 5px;
    }
    .pic {
      width: 111px;
      height: 80px;
      margin-left: 5px;
      &:first-child{
        margin-left: 0;
      }
    }
  }

  // 显示一张小图
  .small {
    height: 75px;
    .title, .footer {
      width: calc(100% - 111px - 15px);
    }
    .title {
      float: left;
      white-space: normal;
      max-height: 2.5em;
    }
    .footer {
      position: absolute;
      bottom: 0;
      left: 0;
      white-space: normal;
      overflow: hidden;
      .tags {
        display: none;
      }
    }
    .pics {
      float: right;
      width: 111px;
      height: 100%;
    }
    .pic {
      height: 100%;
    }
  }

  .no-pic {
    .title {
      white-space: normal;
    }
  }

  .pic {
    position: relative;
    overflow: hidden;
    flex: 1;
    height: 100%;
  }

  .pics {
    overflow: hidden;
    display: flex;
    justify-content: space-between;
  }

  .banner {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  .footer {
    margin-top: 8px;
    display: flex;
    justify-content: space-between;

    .info {
      display: flex;

      & > div {
        margin-left: 8px;
        color: #999;
        white-space: nowrap;
      }
    }
  }
}
</style>
<script>
// import $ from 'webpack-zepto'
import utils from '../libs/utils.js'

export default {
  data() {
    return {
      scroll: true,
      topics: [],
      searchKey: {
        page: 1,
        limit: 20,
        tab: 'all',
        mdrender: true
      },
      searchDataStr: ''
    }
  },
  mounted() {
    this.getTopics()
    // 滚动加载
    $(window).on('scroll', utils.throttle(this.getScrollData, 300, 1000))
  },
  beforeRouteLeave(to, from, next) {
    // 如果跳转到详情页面，则记录关键数据
    // 方便从详情页面返回到该页面的时候继续加载之前位置的数据
    if (to.name === 'topic') {
      // 当前滚动条位置
      window.window.sessionStorage.scrollTop = $(window).scrollTop()
        // 当前页面主题数据
      window.window.sessionStorage.topics = JSON.stringify(this.topics)
    }
    $(window).off('scroll')
    next()
  },
  beforeRouteEnter(to, from, next) {
    if (from.name !== 'topic') {
      // 页面切换移除之前记录的数据集
      if (window.window.sessionStorage.tab) {
        window.window.sessionStorage.removeItem('topics')
      }
    }
    next()
  },
  methods: {
    ready(map) {
      console.log('ready', map)
    },
    setListClass(length, istop) {
      return utils.setListClass(length, istop)
    },
    // 获取主题数据
    getTopics() {
      // let params = $.param(this.searchKey)
      $.get('http://beta.json-generator.com/api/json/get/VJsfUWZ-z?', (d) => {
        this.scroll = true
        if (d && d.data) {
          this.topics = d.data
        }
      })
    },
    // 滚动加载数据
    getScrollData() {
      if (this.scroll) {
        let totalheight = parseInt($(window).height(), 20) + parseInt($(window).scrollTop(), 20)
        if ($(document).height() <= totalheight + 200) {
          this.scroll = false
          this.searchKey.limit += 20
          this.getTopics()
        }
      }
    }
  }
}
</script>



